import { Link } from "react-router-dom";
import "./SendMoney.css";

function SendMoney() {
  var arr = [];
  function funct() {
    for (let i = 1; i < 90; i++) {
      arr[i] = i + 10;
    }
  }
  funct();
  return (
    <body>
      <div class="wrapper12 s1_5">
        <h2>Payment Form</h2>
        <form method="POST">
          <h4>Account</h4>
          <div class="input1-group">
            <div class="input1-box">
              <input
                type="text"
                placeholder="Full Name"
                required
                class="name"
              />
              <i class="fa fa-user icon"></i>
            </div>
            <div class="input1-box">
              <input
                type="email"
                placeholder="Email Adress"
                required
                class="name"
              />
              <i class="fa fa-envelope icon"></i>
            </div>
          </div>

          <div class="input1-group">
            <div class="input1-box">
              <h4> Date of Birth</h4>
              <input type="text" placeholder="DD" class="dob" />
              <input type="text" placeholder="MM" class="dob" />
              <input type="text" placeholder="YYYY" class="dob" />
            </div>
            <div class="input1-box">
              <h4> Gender</h4>
              <input type="radio" id="b1" name="gendar" checked class="radio" />
              <label for="b1">Male</label>
              <input type="radio" id="b2" name="gendar" class="radio" />
              <label for="b2">Female</label>
            </div>
          </div>
          <div class="input1-group">
            <div class="input1-box">
              <h4>Payment Details</h4>
              <input type="radio" name="pay" id="bc1" checked class="radio" />
              <label for="bc1">
                <span>
                  <i class="fa fa-cc-visa"></i> Credit Card
                </span>
              </label>
              <input type="radio" name="pay" id="bc2" class="radio" />
              <label for="bc2">
                <span>
                  <i class="fa fa-cc-paypal"></i> Paypal
                </span>
              </label>
            </div>
          </div>
          <div class="input1-group">
            <div class="input1-box">
              <input
                type="tel"
                placeholder="Card Number"
                required
                class="name"
              />
              <i class="fa fa-credit-card icon"></i>
            </div>
          </div>
          <div class="input1-group">
            <div class="input1-box">
              <input type="tel" placeholder="Card CVC" required class="name" />
              <i class="fa fa-user icon"></i>
            </div>
            <div class="input1-box">
              <select>
                <option value="01" selected>
                  January
                </option>
                <option value="02">February</option>
                <option value="03">March</option>
                <option value="04">April</option>
                <option value="05">May</option>
                <option value="06">June</option>
                <option value="07">July</option>
                <option value="08">August</option>
                <option value="09">September</option>
                <option value="10">October</option>
                <option value="11">November</option>
                <option value="12">December</option>
              </select>
              <select id="years">
                {arr.map((data) => (
                  <option>20{data}</option>
                ))}
              </select>
            </div>
          </div>
          <div class="input1-group">
            <div class="input1-box">
              <Link to="/PaymentSuccess">
                <button type="submit">PAY NOW</button>
              </Link>
            </div>
          </div>
        </form>
      </div>
    </body>
  );
}
export default SendMoney;
