import DataSaverOffIcon from "@mui/icons-material/DataSaverOff";
import LineWeightIcon from "@mui/icons-material/LineWeight";
import SmsIcon from "@mui/icons-material/Sms";
import HouseSidingIcon from "@mui/icons-material/HouseSiding";
import "./Bottom.css";
import { useState } from "react";
import { Link } from "react-router-dom";
function Bottom() {
  const [cla, setclass] = useState("bt0");
  const changeactive = (val) => {
    console.log(val);
    const a = document.getElementById(cla);
    a.classList.remove("activ");
    document.getElementById(val).classList.toggle("activ");
    //  setclass(val);
    setclass(val);
  };
  return (
    <>
      <div className="bottombar">
        <Link to="/histroy">
          <HouseSidingIcon
            className="activ"
            id="bt0"
            onClick={() => {
              changeactive("bt0");
            }}
          />
        </Link>
        <Link to="/exchangerate">
          <DataSaverOffIcon
            id="bt1"
            onClick={() => {
              changeactive("bt1");
            }}
          />
        </Link>
        <Link to="/message">
          <SmsIcon
            id="bt2"
            onClick={() => {
              changeactive("bt2");
            }}
          />
        </Link>
        <Link to="/menu">
          <LineWeightIcon
            id="bt3"
            onClick={() => {
              changeactive("bt3");
            }}
          />
        </Link>
      </div>
    </>
  );
}
export default Bottom;
