import "./ExachangeRate.css";
import country_list from "./CountryList";
import { useEffect, useState } from "react";

function ExachangeRate() {
  const [input, setIntput] = useState(1);
  const [from, setfrom] = useState("USD");
  const [to, setTo] = useState("AED");
  const [exchangeRateTxt, setexchangeRateTxt] = useState();
  useEffect(() => {
    let url = `https://v6.exchangerate-api.com/v6/7193ea84489fca81770a0ae9/latest/${from}`;
    fetch(url)
      .then((response) => response.json())
      .then((result) => {
        console.log(result);
        let exchangeRate = result.conversion_rates[to];
        let totalExRate = (input * exchangeRate).toFixed(2);
        setexchangeRateTxt(`${input} ${from} = ${totalExRate} ${to}`);
      })
      .catch(() => {
        setexchangeRateTxt("Something went wrong");
      });
  }, [input, to, from]);

  return (
    <div className="s1_5">
      <div class="wrapper11 s1_5">
        <h1>Currency Converter</h1>
        <form action="#">
          <div class="amount">
            <p>Enter Amount</p>
            <input
              type="text"
              value={input}
              onChange={(e) => {
                setIntput(e.target.value);
              }}
            />
          </div>
          <div class="drop-list">
            <div class="from">
              <p>From</p>
              <div class="select-box">
                <img
                  src={`https://flagcdn.com/48x36/${(
                    from.charAt(0) + from.charAt(1)
                  ).toLowerCase()}.png`}
                  alt="flag"
                />
                <select
                  value={from}
                  onChange={(e) => {
                    setfrom(e.target.value);
                    // find();
                  }}
                >
                  {Object.keys(country_list).map((data) => (
                    <option>{data}</option>
                  ))}
                </select>
              </div>
            </div>
            <div class="icon">
              <i class="fas fa-exchange-alt"></i>
            </div>
            <div class="to">
              <p>To</p>
              <div class="select-box">
                <img
                  src={`https://flagcdn.com/48x36/${(
                    to.charAt(0) + to.charAt(1)
                  ).toLowerCase()}.png`}
                  alt="flag"
                />
                <select
                  value={to}
                  onChange={(e) => {
                    setTo(e.target.value);
                  }}
                >
                  {Object.keys(country_list).map((data) => (
                    <option>{data}</option>
                  ))}
                </select>
              </div>
            </div>
          </div>
          <div class="exchange-rate">{exchangeRateTxt}</div>
          <button>Get Exchange Rate</button>
        </form>
      </div>
    </div>
  );
}
export default ExachangeRate;
