import "./styles.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";

// import Constant from "./Component/Constant";
import TopBar from "./Component/TopBar";
import Bottom from "./Component/Bottom";
import Menu from "./Component/Menu";
import Setting from "./Component/Setting";
import Message from "./Component/Message";
import { useEffect, useState } from "react";
import SendMoney from "./Component/SendMoney";

import SignUp from "./Component/SignUp";
import SignIn from "./Component/SignIn";
import Update from "./Component/Update";
import PaymentSuccess from "./Component/PaymentSuccess";
import ExachangeRate from "./Component/ExachangeRate";
import TransitionHistroy from "./Component/TransitionHistroy";
import Atm from "./Component/Atm";

export default function App() {
  const [logIn, setLogIn] = useState(false);
  function changeLogIn(bool) {
    console.log("button pressed");
    setLogIn(bool);
    localStorage.setItem("logIn", bool);
  }
  useEffect(() => {
    changeLogIn(localStorage.getItem("logIn"));
  }, []);
  return (
    <>
      <Routes>
        <Route
          path="/signup"
          element={
            <>
              <TopBar />
              <SignUp />
              <Bottom />
            </>
          }
        />
        <Route path="/" element={<SignIn changeLogIn={changeLogIn} />} />
        {logIn === true ? (
          <>
            {" "}
            <Route
              path="/histroy"
              element={
                <>
                  <TopBar />
                  <TransitionHistroy />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/exchangerate"
              element={
                <>
                  <TopBar />
                  <ExachangeRate />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/PaymentSuccess"
              element={
                <>
                  <TopBar />
                  <PaymentSuccess />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/update"
              element={
                <>
                  <TopBar />
                  <Update />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/menu"
              element={
                <>
                  <TopBar />
                  <Menu />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/Setting"
              element={
                <>
                  <TopBar />
                  <Setting />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/message"
              element={
                <>
                  <TopBar />
                  <Message />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/sendmoney"
              element={
                <>
                  <TopBar />
                  <SendMoney />
                  <Bottom />
                </>
              }
            />
            <Route
              path="/atmbranches"
              element={
                <>
                  <TopBar />
                  <Atm />
                  <Bottom />
                </>
              }
            />
          </>
        ) : (
          <Route path="/login" element={<SignIn changeLogIn={changeLogIn} />} />
        )}
      </Routes>
    </>
  );
}
